* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  min-width: 1920px;
  height: 100vh;
  font-family: "Montserrat-Regular", "Arial", sans-serif; }

h2,
h3 {
  margin: 0;
  padding: 0;
  text-transform: uppercase; }

nav {
  margin: 0;
  padding: 0; }

.page {
  display: grid;
  grid-template-areas: "menu order info";
  grid-template-columns: 260px auto 360px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden; }

.wrap {
  width: 1166px;
  margin: 0 auto; }

@font-face {
  font-family: "Montserrat-Regular";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff.woff") format("woff"); }

@font-face {
  font-family: "Montserrat-Bold";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff"); }

.head {
  margin: 0 auto;
  background-color: #ECF2FF; }

.head__wrap {
  margin: 0 60px;
  padding: 20px 0;
  display: grid;
  grid-template-columns: repeat(12, 1fr); }

.head__nav {
  grid-column: 2 / span 6; }

.head__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  text-transform: uppercase; }

.head__link {
  position: relative;
  margin-right: 46px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat-Bold", "Arial, sans-serif";
  text-decoration: none;
  color: #5957DB; }
  .head__link:not(.head__link--logo)::after {
    content: "";
    position: absolute;
    top: 3px;
    right: -20px;
    width: 6px;
    height: 10px;
    background: url("../img/breadcrumb_icon.svg") no-repeat; }
  .head__link.head__link--logo {
    margin-right: 60px; }
  .head__link.head__link--active::after {
    background: none; }

.head__link--active {
  color: #666666; }

.head__btn {
  margin: 0 0 0 auto;
  padding: 14px 50px 14px 24px;
  grid-column: 10 / span 3;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat-Bold", "Arial, sans-serif";
  text-transform: uppercase;
  color: #5957DB;
  background-color: #ffffff;
  border: none;
  border-radius: 8px; }

.head__span {
  position: relative; }
  .head__span::after {
    content: "";
    position: absolute;
    top: 3px;
    right: -24px;
    width: 10px;
    height: 6px;
    background: url("../img/head-btn_icon.svg") no-repeat; }

.menu {
  padding: 20px 0 0 30px;
  grid-area: menu;
  display: grid;
  grid-template-rows: 1fr auto;
  background-color: #5957DB; }

.menu__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.menu__item--active {
  background-color: #ECF2FF;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px; }

.menu__link {
  position: relative;
  padding: 23px 0 23px 60px;
  display: inline-block;
  font-size: 14px;
  line-height: 16px;
  font-family: "Montserrat-Bold", "Arial, sans-serif";
  color: #ffffff;
  text-decoration: none; }
  .menu__link::before {
    content: "";
    position: absolute;
    top: 20px;
    left: 12px;
    width: 18px;
    height: 22px; }

.menu__link--sessions::before {
  background: url("../img/sessions_icon.svg") no-repeat; }

.menu__link--trailers::before {
  background: url("../img/trailers_icon.svg") no-repeat; }

.menu__link--news::before {
  background: url("../img/news_icon.svg") no-repeat; }

.menu__link--about::before {
  background: url("../img/about_icon.svg") no-repeat; }

.menu__link--contacts::before {
  background: url("../img/contacts_icon.svg") no-repeat; }

.menu__link--active {
  color: #5957DB; }
  .menu__link--active.menu__link--sessions::before {
    background: url("../img/sessions-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--trailers::before {
    background: url("../img/trailers-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--news::before {
    background: url("../img/news-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--about::before {
    background: url("../img/about-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--contacts::before {
    background: url("../img/contacts-active_icon.svg") no-repeat; }

.menu__link--profile {
  display: inline-block;
  margin-bottom: 45px;
  padding-left: 82px; }
  .menu__link--profile::before {
    top: 11px;
    width: 40px;
    height: 40px;
    background-color: #ffffff;
    background-image: url("../img/profile_icon.svg");
    background-repeat: no-repeat;
    background-size: 40px 30px;
    background-position: 0 10px;
    border-radius: 50%; }
